import { IContextMenuAction } from './ContextMenu.typings';

export const contextMenuActions: IContextMenuAction = {
  'heading-1': {
    template: '# ',
    insert: str => `# ${str}`,
    startSelection: 2,
    endSelection: 11
  },
  'heading-2': {
    template: '## ',
    insert: str => `## ${str}`,
    startSelection: 3,
    endSelection: 12
  },
  'heading-3': {
    template: '### ',
    insert: str => `### ${str}`,
    startSelection: 4,
    endSelection: 13
  },
  'strong': {
    template: '****',
    insert: str => `**${str}**`,
    startSelection: 2,
    endSelection: 7
  },
  'italic': {
    template: '**',
    insert: str => `*${str}*`,
    startSelection: 1,
    endSelection: 6
  },
  'delete': {
    template: '~~',
    insert: str => `~${str}~`,
    startSelection: 1,
    endSelection: 6
  },
  'marker': {
    template: '``',
    insert: str => `\`${str}\``,
    startSelection: 1,
    endSelection: 7
  },
  'link': {
    template: '[ ](-)',
    insert: str => `[${str}](-)`,
    startSelection: 1,
    endSelection: 11
  },
  'info': {
    template: `
:::info
info[]

:::`,
    insert: str => `
:::info
info[]
${str}
:::`,
    startSelection: 14,
    endSelection: 23,
    MULTI_SELECT: true
  },
  'alert': {
    template: `
:::alert
alert[]

:::`,
    insert: str => `
:::alert
alert[]
${str}
:::`,
    startSelection: 16,
    endSelection: 25,
    MULTI_SELECT: true
  },
  'tip': {
    template: `
:::tip
tip[]

:::`,
    insert: str => `
:::tip
tip[]
${str}
:::`,
    startSelection: 12,
    endSelection: 21,
    MULTI_SELECT: true
  },
  'caution': {
    template: `
:::caution
caution[]

:::`,
    insert: str => `
:::caution
caution[]
${str}
:::`,
    startSelection: 20,
    endSelection: 29,
    MULTI_SELECT: true
  },
  'code': {
    template: `\`\`\`js

\`\`\``,
    insert: str => `\`\`\`js
${str}
\`\`\``,
    startSelection: 6,
    endSelection: 9
  },
  'unordered-list': {
    template: `
- \n- \n- `,
    insert: str => `- ${str}`,
    startSelection: 3,
    endSelection: 10
  },
  'ordered-list': {
    template: `
1. \n2. \n3. `,
    insert: str => `1. ${str}`,
    startSelection: 4,
    endSelection: 11
  },
  'image': {
    template: '![]()',
    insert: str => `![](${str})`,
    startSelection: 2,
    endSelection: 7,
    MULTI_SELECT: true
  },
  'video': {
    template: '![]()',
    insert: str => `![](${str})`,
    startSelection: 9,
    endSelection: 15
  },
  'table': {
    template: `|  |   |
| - | - | 
|  |  |`,
    insert: str => str,
    startSelection: 2,
    endSelection: 11
  },
  'quiz': {
    template: `\`\`\`quiz[ ]

\`\`\``,
    insert: str => str,
    startSelection: 8,
    endSelection: 22,
  }
};
